<template>
  <label class="switch" @click="handleSwitchClick">
    <span class="switch__slider sun">
      <div class="crescent"></div>
    </span>
  </label>
</template>

<script>
export default {
  name: "switch_component",
  methods: {
    handleSwitchClick(e) {
      if (!e.srcElement.children[0].classList.contains("active")) {
        e.srcElement.children[0].classList.add("active");
        document.documentElement.dataset.id = "917acd2";
      } else {
        e.srcElement.children[0].classList.remove("active");
        document.documentElement.dataset.id = "13pzd77";
      }
      document.documentElement.classList.add("fd377dy");
      window.setTimeout(() => {
        document.documentElement.classList.remove("fd377dy");
      }, 600);
    }
  }
};
</script>

<style scoped>
.switch {
  position: relative;
  top: 3px;
  display: inline-block;
  width: 75px;
  height: 30px;
  cursor: pointer;
}

.switch__slider {
  width: 100%;
  height: 100%;
  position: relative;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  pointer-events: none;
  background-color: var(--bg-panel);
  border-radius: 34px;
  -webkit-transition: transform 0.6s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: transform 0.6s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.switch__slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 7px;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 50%;
}

.switch__slider.sun.active > .crescent {
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  transform: scale(1);
  background: var(--bg-panel);
}

.switch__slider.active:before {
  -webkit-transform: translate(205%, -50%);
  -ms-transform: translate(205%, -50%);
  transform: translate(205%, -50%);
  background: #8983f7;
  background: linear-gradient(40deg, #8983f7, #a3dafb 70%);
}

.sun:before {
  background: #ff0080;
  background: linear-gradient(40deg, #ff0080, #ff8c00 70%);
  -webkit-transition: transform 0.6s cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: transform 0.6s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.crescent {
  position: absolute;
  top: 3px;
  right: 5px;
  border-radius: 100%;
  width: 16px;
  height: 16px;
  -webkit-transform: scale(0);
  -ms-transform: scale(0);
  transform: scale(0);
  transform-origin: top right;
  transition: transform 0.6s cubic-bezier(0.645, 0.045, 0.355, 1);
}
</style>
